export default class FormValidator {
  constructor(configValidation, formElement) {
    this._inputSelector = configValidation.inputSelector;
    this._submitButtonSelector = configValidation.submitButtonSelector;
    this._inactiveButtonClass = configValidation.inactiveButtonClass;
    this._inputErrorClass = configValidation.inputErrorClass;
    this._errorClass = configValidation.errorClass;

    this._formElement = formElement;

    this._inputList = Array.from(
      this._formElement.querySelectorAll(this._inputSelector)
    );
    this._buttonElement = this._formElement.querySelector(
      this._submitButtonSelector
    );
  }

  // –ø–æ–∫–∞–∑–∞—Ç—å –æ—à–∏–±–∫—É
  _showInputError(inputElement) {
    const errorElement = this._formElement.querySelector(
      `.${inputElement.id}-error`
    );
    errorElement.textContent = inputElement.validationMessage;
    errorElement.classList.add(this._errorClass);
    inputElement.classList.add(this._inputErrorClass);
  }

  // —Å–∫—Ä—ã—Ç—å –æ—à–∏–±–∫—É
  _hideInputError(inputElement) {
    const errorElement = this._formElement.querySelector(
      `.${inputElement.id}-error`
    );
    errorElement.textContent = "";
    errorElement.classList.remove(this._errorClass);
    inputElement.classList.remove(this._inputErrorClass);
  }

  // –ø—Ä–æ–≤–µ—Ä–∏—Ç—å –Ω–∞ –≤–∞–ª–∏–¥–Ω–æ—Å—Ç—å –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–µ –ø–æ–ª–µ –≤–≤–æ–¥–∞
  _checkInputValidity(inputElement) {
    if (!inputElement.validity.valid) {
      this._showInputError(inputElement);
    } else {
      this._hideInputError(inputElement);
    }
  }

  // –Ω–∞–π—Ç–∏ —Å—Ä–µ–¥–∏ –≤—Å–µ—Ö –ø–æ–ª–µ–π –≤–≤–æ–¥–∞ —Ö–æ—Ç—è –±—ã –æ–¥–Ω–æ –Ω–µ–≤–∞–ª–∏–¥–Ω–æ–µ –ø–æ–ª–µ
  // –¥–ª—è –Ω–∞—Å—Ç—Ä–æ–π–∫–∏ —Å—Ç–∞—Ç—É—Å–∞ –∫–Ω–æ–ø–∫–∏
  _hasInvalidInput() {
    return this._inputList.some((inputElement) => {
      return !inputElement.validity.valid;
    });
  }

  // –∑–∞–±–ª–æ–∫–∏—Ä–æ–≤–∞—Ç—å –∫–Ω–æ–ø–∫—É
  _disableButton() {
    this._buttonElement.classList.add(this._inactiveButtonClass);
    this._buttonElement.setAttribute("disabled", true);
  }

  // –∞–∫—Ç–∏–≤–∏—Ä–æ–≤–∞—Ç—å –∫–Ω–æ–ø–∫—É
  _enableButton() {
    this._buttonElement.classList.remove(this._inactiveButtonClass);
    this._buttonElement.removeAttribute("disabled");
  }

  // –∏–∑–º–µ–Ω–∏—Ç—å —Å—Ç–∞—Ç—É—Å –∫–Ω–æ–ø–∫–∏
  _toggleButtonState() {
    // –µ—Å–ª–∏ —Ö–æ—Ç—è –±—ã –æ–¥–Ω–æ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –ø—É—Å—Ç–æ–µ –∏–ª–∏ –Ω–µ–≤–∞–ª–∏–¥–Ω–æ, —Å–¥–µ–ª–∞—Ç—å –∫–Ω–æ–ø–∫—É –Ω–µ–∞–∫—Ç–∏–≤–Ω–æ–π
    if (this._hasInvalidInput()) {
      this._disableButton();
      // –∏–Ω–∞—á–µ - –∞–∫—Ç–∏–≤–Ω–æ–π
    } else {
      this._enableButton();
    }
  }

  // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —Å–ª—É—à–∞—Ç–µ–ª–∏ –Ω–∞ –≤—Å–µ –ø–æ–ª—è –≤–≤–æ–¥–∞ –∏ submit —Ñ–æ—Ä–º—ã
  // —Ñ–æ—Ä–º—É –ø–µ—Ä–µ–¥–∞–µ–º —á–µ—Ä–µ–∑ formElement
  _setEventListeners() {
    this._inputList.forEach((inputElement) => {
      inputElement.addEventListener("input", () => {
        this._checkInputValidity(inputElement);
        this._toggleButtonState();
      });
    });

    this._formElement.addEventListener("submit", (evt) => {
      evt.preventDefault();
    });
  }

  // —Å–±—Ä–æ—Å –≤–∞–ª–∏–¥–∞—Ü–∏–∏
  resetValidation() {
    // –∫–Ω–æ–ø–∫–∞ –Ω–µ–∞–∫—Ç–∏–≤–Ω–∞
    this._toggleButtonState();
    // –æ—à–∏–±–∫–∏ –Ω–µ –ø–æ–∫–∞–∑—ã–≤–∞—Ç—å
    this._inputList.forEach((inputElement) => {
      this._hideInputError(inputElement);
    });
  }

  // –≤–∞–ª–∏–¥–∞—Ü–∏—è —Ñ–æ—Ä–º—ã
  enableValidation() {
    this._setEventListeners();
  }
}

