export default class Card {
  constructor(cardData, userId, templateSelector, handleCardClick, { handleLikeClick, handleDeleteClick }) {
    this._name = cardData.name;
    this._link = cardData.link;
    this._likes = cardData.likes;
    this._cardId = cardData._id;
    this._ownerId = cardData.owner._id;
    this._userId = userId;
    this._templateSelector = templateSelector;
    this._handleCardClick = handleCardClick;
    this._handleLikeClick = handleLikeClick;
    this._handleDeleteClick = handleDeleteClick;
  }

  _getCardElement() {
    const cardElement = document
      .querySelector(this._templateSelector)
      .content
      .querySelector(".card")
      .cloneNode(true);

    return cardElement;
  }

  // –ø–æ—Å—Ç–∞–≤–∏—Ç—å –ª–∞–π–∫
  putLike(likesArray) {
    this._likeCounter.textContent = likesArray.length;
    this._buttonLike.classList.add('card__like_active');
    this.isLiked = true;
  }

  // —É–¥–∞–ª–∏—Ç—å –ª–∞–π–∫
  deleteLike(likesArray) {
    this._buttonLike.classList.remove('card__like_active');
    this._likeCounter.textContent = likesArray.length;
    this._isLiked = false;
  }

  // —É–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É
  deleteCard() {
    this._card.remove();
    this._card = null;
  }

  // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —Å–ª—É—à–∞—Ç–µ–ª–∏ –ø–æ –∫–ª–∏–∫—É –Ω–∞ –∫–æ—Ä–∑–∏–Ω—É, –ª–∞–π–∫, –∫–∞—Ä—Ç–∏–Ω–∫—É
  _setEventListeners() {
    this._buttonDelete.addEventListener('click', () => {
    this._handleDeleteClick(this._cardId);
    });

    this._buttonLike.addEventListener('click', () => {
      this._handleLikeClick(this._cardId, this._isLiked);
    });

    this._cardImage.addEventListener('click', () => {
      this._handleCardClick(this._cardImage.src, this._cardImage.alt);
    });
  }

  // –∑–∞–ø–æ–ª–Ω–∏—Ç—å —à–∞–±–ª–æ–Ω –∫–∞—Ä—Ç–æ—á–∫–∏ –¥–∞–Ω–Ω—ã–º–∏, –¥–æ–±–∞–≤–∏—Ç—å —Å–ª—É—à–∞—Ç–µ–ª–∏
  generateCard() {
    this._card = this._getCardElement();

    this._cardImage = this._card.querySelector('.card__image');
    this._cardName = this._card.querySelector('.card__name');
    this._buttonDelete = this._card.querySelector('.card__delete');
    this._buttonLike = this._card.querySelector('.card__like');
    this._likeCounter = this._card.querySelector('.card__like-counter');

    this._likeCounter.textContent = this._likes.length;

    if (this._likes.some((user) => user._id === this._ownerId)) {
      this._isLiked = true;
      this._buttonLike.classList.add('card__like_active');
    } else {
      this._isLiked = false;
      this._buttonLike.classList.remove('card__like_active');
    }

    if (this._ownerId !== this._userId) {
      this._buttonDelete.remove();
    }

    this._setEventListeners();

    this._cardName.textContent = this._name;
    this._cardImage.alt = this._name;
    this._cardImage.src = this._link;

    return this._card;
  }
}



