import './index.css';
import Api from "../components/Api";
import Section from '../components/Section.js';
import Card from '../components/Card.js';
import PopupWithForm from '../components/PopupWithForm.js';
import PopupWithImage from '../components/PopupWithImage.js';
import PopupWith–°onfirmation from '../components/PopupWith–°onfirmation.js';
import UserInfo from '../components/UserInfo.js';
import FormValidator from '../components/FormValidator.js';
import {
  configValidation,
  popupEditProfileSelector,
  popupAvatarSelector,
  popupAddCardSelector,
  popupCardImageSelector,
  popupFormConfirmationSelector,
  userNameSelector,
  userAboutSelector,
  userAvatarSelector,
  buttonOpenEditProfilePopup,
  buttonOpenAvatarPopup,
  buttonOpenAddCardPopup,
  formEditProfile,
  formAvatar,
  formCard,
  cardTemplateSelector,
  cardsContainerSelector
} from '../utils/constants.js';

let userId;

const api = new Api({
  baseUrl: 'https://mesto.nomoreparties.co/v1/cohort-64',
  headers: {
    authorization: 'a24e7087-a96b-4b2b-8905-f0302e2d2b76',
    'Content-Type': 'application/json'
  }
}); 

// –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è

// –ø–æ–ª—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ —Å —Å–µ—Ä–≤–µ—Ä–∞
api.getUserInfo()
  .then((userData) => {
    userInfo.setUserInfo(userData);
    userId = userData._id;
  })
  .catch((err) => {
    console.log(`–û—à–∏–±–∫–∞: ${err}`);
  }); 

// –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è
const handleEditProfile = () => {
  const {name, about} = userInfo.getUserInfo();
  formEditProfile.name.value = name;
  formEditProfile.about.value = about;
  formProfileValidator.resetValidation();
  popupEditProfile.open();
};

// –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ submit –ø—Ä–æ—Ñ–∏–ª—è
const handleSubmitProfile = (userData) => {
  popupEditProfile.renderLoading(true);
  // –∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä: –æ–±–Ω–æ–≤–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ
  api.patchUserInfo(userData)
    .then((userData) => {
      userInfo.setUserInfo(userData);
      popupEditProfile.close();
    })
    .catch((err) => {
      console.log(`–û—à–∏–±–∫–∞: ${err}`);
    })
    .finally(() => {
      popupEditProfile.renderLoading(false);
    })
};

// –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–µ–∫

// –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –∫–∞—Ä—Ç–∏–Ω–∫–µ –∫–∞—Ä—Ç–æ—á–∫–∏ (–æ—Ç–∫—Ä—ã—Ç—å)
const handleCardClick = (cardImageSrc, cardImageAlt) => {
  popupCardImage.open(cardImageSrc, cardImageAlt);
};

// –¥–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É
const renderCard = (cardData) => {
  const card = new Card(
    cardData,
    userId,
    cardTemplateSelector,
    handleCardClick,
    {handleLikeClick: (cardId, isLiked) => {
        if (isLiked) {
          // –∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä: —É–¥–∞–ª–∏—Ç—å –ª–∞–π–∫
          api.deleteLike(cardId)
            .then((cardData) => {
              card.deleteLike(cardData.likes);
            })
            .catch((err) => {
              console.log(`–û—à–∏–±–∫–∞: ${err}`);
            });
        } else {
          // –∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä: –ø–æ—Å—Ç–∞–≤–∏—Ç—å –ª–∞–π–∫
          api.putLike(cardId)
            .then((cardData) => {
              card.putLike(cardData.likes);
            })
            .catch((err) => {
              console.log(`–û—à–∏–±–∫–∞: ${err}`);
            });
        }
      },
      handleDeleteClick: (cardId) => {
        popupFormConfirmation.open();
        popupFormConfirmation.handleSubmit(() => {
          // –∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä: —É–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É
          api.deleteCard(cardId)
            .then((cardData) => {
              card.deleteCard(cardData._id);
              popupFormConfirmation.close();
            })
            .catch((err) => {
              console.log(`–û—à–∏–±–∫–∞: ${err}`);
            });
        })
      }
    },
  );
  const generatedCard = card.generateCard();
  cardsContainer.addItem(generatedCard);
};


// –æ—Ç–∫—Ä—ã—Ç—å —Ñ–æ—Ä–º—É –ù–æ–≤–æ–µ –º–µ—Å—Ç–æ
const handleAddCard = () => {
  formCardValidator.resetValidation();
  popupAddCard.open();
};

// –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ submit/–∑–∞–∫—Ä—ã—Ç–∏—è —Ñ–æ—Ä–º—ã "–ù–æ–≤–æ–µ –º–µ—Å—Ç–æ"
const handleSubmitCard = (cardData) => {
  popupAddCard.renderLoading(true);
  // –∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä: –¥–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É
  api.postCard(cardData)
    .then((cardData) => {
      renderCard(cardData);
      popupAddCard.close();
    })
    .catch((err) => {
      console.log(`–û—à–∏–±–∫–∞: ${err}`);
    })
    .finally(() => {
      popupAddCard.renderLoading(false);
    })
};

// —Å–µ—Ä–≤–µ—Ä: –∑–∞–≥—Ä—É–∑–∏—Ç—å –º–∞—Å—Å–∏–≤ –∫–∞—Ä—Ç–æ—á–µ–∫
api.getCards()
  .then((cards) => {
    cardsContainer.renderItems(cards);
  })
  .catch((err) => {
    console.log(`–û—à–∏–±–∫–∞: ${err}`);
  });

  // –û–±–Ω–æ–≤–ª–µ–Ω–∏–µ –∞–≤–∞—Ç–∞—Ä–∞

  // –æ—Ç–∫—Ä—ã—Ç—å —Ñ–æ—Ä–º—É "–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä"
  const handleOpenAvatar = () => {
    formAvatarValidator.resetValidation();
    popupAvatar.open();
  };
  
  // submit + –∑–∞–∫—Ä—ã—Ç—å —Ñ–æ—Ä–º—É "–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä"
  const handleSubmitAvatar = (userData) => {
    popupAvatar.renderLoading(true);
    // –∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä: –æ–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    api.patchAvatar(userData)
      .then((userData) => {
        userInfo.setUserAvatar(userData);
        popupAvatar.close();
      })
      .catch((err) => {
        console.log(`–û—à–∏–±–∫–∞: ${err}`);
      })
      .finally(() => {
        popupAvatar.renderLoading(false);
      })
  };

// –≠–∫–∑–µ–º–ø–ª—è—Ä—ã –∫–ª–∞—Å—Å–æ–≤

const userInfo = new UserInfo(userNameSelector, userAboutSelector, userAvatarSelector);
const cardsContainer = new Section({ renderer: renderCard }, cardsContainerSelector);
const popupEditProfile = new PopupWithForm(popupEditProfileSelector, handleSubmitProfile);
const popupAvatar = new PopupWithForm(popupAvatarSelector, handleSubmitAvatar);
const popupFormConfirmation = new PopupWith–°onfirmation(popupFormConfirmationSelector);
const popupAddCard = new PopupWithForm(popupAddCardSelector,handleSubmitCard);
const popupCardImage = new PopupWithImage(popupCardImageSelector);
const formProfileValidator = new FormValidator(configValidation, formEditProfile);
const formCardValidator = new FormValidator(configValidation, formCard);
const formAvatarValidator = new FormValidator(configValidation, formAvatar); 

// –í–ê–õ–ò–î–ê–¶–ò–Ø –§–û–†–ú

formProfileValidator.enableValidation();
formCardValidator.enableValidation();
formAvatarValidator.enableValidation();

// –°–õ–£–®–ê–¢–ï–õ–ò –°–û–ë–´–¢–ò–ô

buttonOpenEditProfilePopup.addEventListener('click', handleEditProfile);
buttonOpenAddCardPopup.addEventListener('click', handleAddCard);
buttonOpenAvatarPopup.addEventListener('click', handleOpenAvatar);
popupEditProfile.setEventListeners();
popupAvatar.setEventListeners();
popupFormConfirmation.setEventListeners();
popupAddCard.setEventListeners();
popupCardImage.setEventListeners();